import Vue from 'vue';
//
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'

     import users from '@/views/modules/users/list'
    import dangfeishoujiao from '@/views/modules/dangfeishoujiao/list'
    import dictionary from '@/views/modules/dictionary/list'
    import forum from '@/views/modules/forum/list'
    import gonggao from '@/views/modules/gonggao/list'
    import news from '@/views/modules/news/list'
    import yonghu from '@/views/modules/yonghu/list'
    import config from '@/views/modules/config/list'
    import dictionaryDangfeishoujiao from '@/views/modules/dictionaryDangfeishoujiao/list'
    import dictionaryForumState from '@/views/modules/dictionaryForumState/list'
    import dictionaryGonggao from '@/views/modules/dictionaryGonggao/list'
    import dictionaryNews from '@/views/modules/dictionaryNews/list'
    import dictionarySex from '@/views/modules/dictionarySex/list'





//2.   
const routes = [{
    path: '/index',
    name: '',
    component: Index,
    children: [{
      // main
      path: '/',
      name: '',
      component: Home,
      meta: {icon:'', title:'center'}
    }, {
      path: '/updatePassword',
      name: '',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '',
      component: center,
      meta: {icon:'', title:'center'}
    } ,{
        path: '/users',
        name: '',
        component: users
      }
    ,{
        path: '/dictionaryDangfeishoujiao',
        name: '',
        component: dictionaryDangfeishoujiao
    }
    ,{
        path: '/dictionaryForumState',
        name: '',
        component: dictionaryForumState
    }
    ,{
        path: '/dictionaryGonggao',
        name: '',
        component: dictionaryGonggao
    }
    ,{
        path: '/dictionaryNews',
        name: '',
        component: dictionaryNews
    }
    ,{
        path: '/dictionarySex',
        name: '',
        component: dictionarySex
    }
    ,{
        path: '/config',
        name: '',
        component: config
    }


    ,{
        path: '/dangfeishoujiao',
        name: '',
        component: dangfeishoujiao
      }
    ,{
        path: '/dictionary',
        name: '',
        component: dictionary
      }
    ,{
        path: '/forum',
        name: '',
        component: forum
      }
    ,{
        path: '/gonggao',
        name: '',
        component: gonggao
      }
    ,{
        path: '/news',
        name: '',
        component: news
      }
    ,{
        path: '/yonghu',
        name: '',
        component: yonghu
      }


    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '/',
    name: '',
    redirect: '/index'
  }, /**/
  {
    path: '*',
    component: NotFound
  }
]
//3.VueRouter  
const router = new VueRouter({
  mode: 'hash',
  /*hashhistory*/
  routes //  routes: routes
})

export default router;
